.feedback_container{
    width: 100%;
    background-color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-bottom: 3vw;
}
.feedback_title{
    margin: 3vw;
    font-size: 2vw;
}
.feedback_box{
    width: 70%;
    height: 40vw;
    background: url(../../img/setka.png);
    background-repeat: no-repeat;
    background-size: 110%;
    background-position: -3vw -3vw;
    box-shadow: inset 0px 0px 3vw 3vh #000000;;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2vw 5vw;
}