.stack_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: #000000;
}
.stack_h1{
    margin: 3vw;
    font-size: 2vw;
}
.stack_box{
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    width: 70%;
    height: 100%;
    background: url(../../img/setka.png);
    background-size: 110%;
    background-position: -3vw -3vw;
    background-repeat: no-repeat;
    padding: 2vw 5vw;
    box-shadow: inset 0px 0px 3vw 3vh #000000;
}
.stack_h2{
    margin-bottom: 1.5vw;
    font-size: 1.7vw;
}
.stack_text_box{
    margin-top: 2vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.stack_text_about_me{
    width: 100%;
    font-size: 1.4vw;
    margin-bottom: 2vw;
}
.icons_wrap{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.icons_row{
    display: flex;
    justify-content: space-around;
    margin: 1vw 0;
}
.rectangle_margin{
    margin: 0 3vw;
}
.rectangle_wrap{
    width: 4vw;
    height: 7vw;
    margin: 0vw 3vw;
    background: none;
    position: relative;
    display: flex;
    justify-content: center;
    transition: 0.8s;
    z-index: 5;
    pointer-events: none;
}
.rectangle_wrap:before, .rectangle_wrap:after{
    transition: 1s;
    content: "";
    display: block;
    width: inherit;
    height: inherit;
    pointer-events: all;
}
.rectangle_wrap:before{
    background: #181818;
    transform: rotateZ(60deg);
}
.rectangle_wrap:after{
    top: 0;
    position: absolute;
    background: #2c2c2c;
    transform: rotateZ(-60deg);
}
.rectangle_wrap:hover:after{
    transform: rotateZ(-120deg);
}
.rectangle_wrap:hover:before{
    transform: rotateZ(120deg);
}
.work_icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;
    width: 100%; 
}
.work_icons{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;
    width: 125%;
}