.myworks_container{
    width: 100%;
    background-color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.myworks_title{
    margin-top: 3vw;
    font-size: 2vw;
}
.work_buttons{
    display: flex;

}
.button{
    font-size: 2vw;
    padding: 2vw;
    cursor: pointer;
    color: rgb(169,169,169);
}
.button:hover{
    background: grey;
}
.work_container{
    width: 70%;
    height: 40vw;
    background: url(../../img/setka.png);
    background-repeat: no-repeat;
    background-size: 110%;
    background-position: -3vw -3vw;
    box-shadow: inset 0px 0px 3vw 3vh #000000;;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2vw 5vw;
}
.work_wbcontainer{
    display: flex;
    flex-direction: column;
}
.work_description{
    font-size: 1.5vw;
}
.work_img{
    border-radius: 1vw;
    z-index: 11;
    width: 15vw;
    margin: 1vw 1vw;
    cursor: pointer;
    transition: 0.5s;
    transform-origin: center center;
}
.wb{
    width: 22.5vw;
}
.work_screenshots_box{
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.75);
    display: flex;
    pointer-events: none;
    opacity: 0;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
}
.work_screenshots_container{
    display: flex;
    flex-direction: column;
    height: 80%;
    background: #000000;
    box-shadow: 0px 0px 3vw 2vw #000000;
    overflow-y: scroll;
}
.work_screenshots_container::-webkit-scrollbar{
    background: none;
    width: 3px;
}
.work_screenshots_container::-webkit-scrollbar-thumb{
    background: #ffffff;
    border-radius: 5vw;
}
.work_screenshots{
    width: 20vw;
    margin: 1vw 2vw;
}
.work_screenshots2{
    width: 70vw;
    margin: 1vw 2vw;
}