.greetings_container{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #32003b;
    transform-style: preserve-3d;
    perspective: 1800px;
    color: #ffffff;
    position: fixed;
    z-index: 100;
    overflow: hidden;
}
.greetings_container > p{
    transition: 0.5s;
}
.setka{
    z-index: 2;
    background: #74008b;
    box-shadow: 10vw 0 5vw #74008b;
    border-radius: 50vw;
    width: 100vw;
    height: 100vh;
    position: absolute;
    transition: 1.5s;
    transition-delay: 0.75s;
    transition-timing-function: cubic-bezier(1,.01,1,.41);
}
#left_setka{
    transform: rotateY(90deg) translateX(-40vw) translateZ(-35vw) scale(1.4);
}
#right_setka{
    transform: rotateY(90deg) translateX(-40vw) translateZ(35vw) scale(1.4);
}
#button_letsgo{
    z-index: 3;
    padding: 10px 40px;
    font-size: 30px;
    font-weight: 900;
    cursor: pointer;
    background: none;
    border: 4px solid #ffffff;
    color: #ffffff;
    transition: 0.5s;
}
#button_letsgo:hover{
    border: 4px solid #74008b;
    background: #74008b;
}