.blockbuster_container{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    background-image: url(../../img/setka.png);
    background-position: -5vw -2vh;
    background-size: 110%;
    background-repeat: no-repeat;
    background-color: #000000;
    box-shadow: inset 0px -20px 2vw 0vh #000000;
}
.spotlight{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0vh;
    left: 0vw;
    background: radial-gradient(transparent, #000000 90%);
}
.glowing {
    position: absolute;
    width: 50vw;
    height: 20vh;
    left: 0vw;
    top: 0vh;
    z-index: 10;
    transform-origin: left;
    animation: colorChange 30s ease-in-out infinite;
}
.glowing:nth-child(even) {
    transform-origin: left;
    left: 85vw;
    top: 80vh;
    animation: colorChangeReverse 60s ease-in-out infinite;
}
@keyframes colorChange {
    0% {
      filter: hue-rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      filter: hue-rotate(360deg);
      transform: rotate(360deg);
    }
}
@keyframes colorChangeReverse {
    0% {
        filter: hue-rotate(0deg);
        transform: rotate(360deg);
    }
    100% {
        filter: hue-rotate(360deg);
        transform: rotate(0deg);
    }
}
.gl1{
    position: absolute;
    top: 50%;
    left: 0px;
    width: 1vw;
    height: 1vw;
    border-radius: 50%;
    background: rgba(134,255,0,1);
    box-shadow: 0 0 20px rgba(134,255,0,1),
      0 0 40px rgba(134,255,0,1),
      0 0 60px rgba(134,255,0,1),
      0 0 80px rgba(134,255,0,1),
      0 0 0 8px rgba(134,255,0,.1);
}
.gl2{
    position: absolute;
    top: 50%;
    left: 150px;
    width: 1vw;
    height: 1vw;
    border-radius: 50%;
    background: rgba(255,214,0,1);
    box-shadow: 0 0 20px rgba(255,214,0,1),
      0 0 40px rgba(255,214,0,1),
      0 0 60px rgba(255,214,0,1),
      0 0 80px rgba(255,214,0,1),
      0 0 0 8px rgba(255,214,0,.1);
}
.gl3{
    position: absolute;
    top: 50%;
    left: 300px;
    width: 1vw;
    height: 1vw;
    border-radius: 50%;
    background: rgba(0,226,255,1);
    box-shadow: 0 0 20px rgba(0,226,255,1),
      0 0 40px rgba(0,226,255,1),
      0 0 60px rgba(0,226,255,1),
      0 0 80px rgba(0,226,255,1),
      0 0 0 8px rgba(0,226,255,.1);
}
.gl1 {
    animation: animate 21s alternate ease-in-out infinite;
  }
.gl2 {
    animation: animate-reverse 14s alternate ease-in-out infinite;
  }
.gl3 {
    animation: animate 25s alternate ease-in-out infinite; 
}
@keyframes animate {
    0% {
      transform: rotate(180deg) translateX(50px);
    }
    50% {
      transform: rotate(0deg) translateX(0px);
    }
    100% {
      transform: rotate(360deg) translateX(150px);
    }
}
@keyframes animate-reverse {
    0% {
      transform: rotate(360deg) translateX(100px) translateY(150px);
    }
    
    50% {
      transform: rotate(180deg) translateX(250px) translateY(100px);
    }
    
    100% {
      transform: rotate(0deg) translateX(0px) translateY(50px);
    }
}
.blockbucter_h1{
    font-size: 2.5vw;
    z-index: 5;
}
.blockbuster_h2{
    margin-top: 7vw;
    font-size: 2vw;
    z-index: 5;
}
.navbar{
    background: rgb(43, 43, 43, 0.75);
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 0 0 10vw 10vw;
    color: #ffffff;
    overflow: hidden;
    transition: 0.5s;
    transition-delay: 1s;
    z-index: 100;
}
.nav_button{
  padding: 1vw 3vw;
  cursor: pointer;
  transition: 0.25s;
}
.nav_button:hover{
  background: rgba(97, 97, 97, 0.98);
}
.torch{
  height: 3vw;
  width: 0.2vw;
  border-radius: 5vw;
  background: #000000;
}

